import './Profile.css';
import MyPostsContainer from './MyPosts/MyPostsContainer';
// import Jpg from './../../imgs/babyYoda.jpg';
import Preloader from './../common/Preloader/Preloader';
import ProfileStatus from './ProfileStatus';
import profile_default_pic from '../../imgs/profile_default_pic.png'
import { Field, reduxForm } from 'redux-form';
import { Element } from './../common/FormControl/FormControls';
import { useState } from 'react';

let myData = {
  aboutMe: "asdf",
  contacts: {
    facebook: null,
    website: null,
    vk: null,
    twitter: null,
    instagram: null,
    youtube: "",
    github: null,
    mainLink: "fruzi.io"
  },
  lookingForAJob: true,
  lookingForAJobDescription: "qwertyu",
  fullName: "datebayoo",
  userId: 13751,
}

const Profile = (props) => {
  let [editMode, setEditMode] = useState(false)

  const onSubmit = (formData) => {
    console.log(formData)
    props.updateProfileInfo(formData)
    onClick()
  }

  const onClick = () => {
    setEditMode(!editMode)
  }

  if (!props.state.profile) {
    return <Preloader />
  }

  return (
    <div className="profile">
      <ProfileStatus status={props.state.status} updateStatus={props.updateStatus} />
      {/* <div className="profile_item bgc__transparent">
        <img className="profile_bg_img" src="https://www.w3schools.com/howto/img_snow_wide.jpg" alt="" />
      </div> */}
      <div className="profile_inner profile_item">
        <img className="avatar_img"
          src={props.state.profile.photos.large
            ? props.state.profile.photos.large
            : profile_default_pic}
          alt="" />
        {editMode
          ? <ProfileReduxForm onSubmit={onSubmit} initialValues={props.state.profile} {...props} />
          : <ProfileInfo onClick={onClick} {...props} />
        }
      </div>
      <MyPostsContainer />
    </div>
  )
}

const ProfileInfo = (props) => {
  let contacts = Object.keys(props.state.profile.contacts).map(item =>
    <div className="profile_info__item">{item}:
      <a href={props.state.profile.contacts[item]}><span>{props.state.profile.contacts[item]}</span></a>
    </div>
  )

  return <div className="profile_info">
    <div className="profile_info_inner">
      <div className="profile_name profile_title">{props.state.profile.fullName}</div>
      <div className="profile_info__item">Looking for a job:
        <span>{props.state.profile.lookingForAJob ? 'Yes' : 'No'}</span>
      </div>
      <div className="profile_info__item">Looking for a job description:
        <span>{props.state.profile.lookingForAJobDescription}</span>
      </div>
      <div className="profile_info__item">About me:
        <span>{props.state.profile.aboutMe}</span>
      </div>
    </div>
    <div className="profile_info_inner">

      {contacts}

    </div>
    <button className='posts_btn' onClick={props.onClick}>Redact profile</button>
  </div>
}

const Input = Element('input')

const ProfileForm = (props) => {
  let contacts = Object.keys(props.state.profile.contacts).map(item =>
    <div className="profile_info__item">
      {/* <a href={props.state.profile.contacts[item]}><span>{props.state.profile.contacts[item]}</span></a> */}
      <Field type='text' placeholder={item} name={'contacts.' + item} component={Input} />
    </div>
  )

  return (
    <form className='profile_info' onSubmit={props.handleSubmit}>
      <div className="profile_info_inner">
        <Field type='text' placeholder={'fullName'} name={'fullName'} component={Input} />
        <div className="profile_info__item">
          <label htmlFor="lookingForAJob">Looking for a job</label>
          <Field type='checkbox' name={'lookingForAJob'} id={'lookingForAJob'} component={Input} />
        </div>
        <div className="profile_info__item">
          <Field type='text' placeholder={'lookingForAJobDescription'} name={'lookingForAJobDescription'} component={Input} />
        </div>
        <div className="profile_info__item">
          <Field type='text' placeholder={'aboutMe'} name={'aboutMe'} component={Input} />
        </div>
      </div>
      <div className="profile_info_inner">

        {contacts}

      </div>
      {props.error && <div>
        {props.error}
      </div>}
      <div>
        <button className='posts_btn' type="submit">Confirm</button>
      </div>
    </form>
  )
}

const ProfileReduxForm = reduxForm({ form: 'ProfileInfo', enableReinitialize: true })(ProfileForm)

export default Profile